import React from 'react';
import './App.css';
import Break from './break';
import Session from './session';
import Timer from './timer';
// import count from './count';

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      breakCount: 5,
      sessionCount: 25,
      label: "Session",
      counting: true,
      minutes: 1,
      seconds: 3
    }
  }
  onBreakChange = this.onBreakChange.bind(this);
  onSessionChange = this.onSessionChange.bind(this);
  onReset = this.onReset.bind(this);
  onStartStop = this.onStartStop.bind(this);
  
  onBreakChange(newBreakCount) {
    this.setState({
      breakCount: newBreakCount
    });
    if (this.state.label === "Break") {
      this.setState({
        minutes: newBreakCount
      });
    }
  }
  onSessionChange(newSessionCount) {
    this.setState({
      sessionCount: newSessionCount
    });
    if (this.state.label === "Session") {
      this.setState({
        minutes: newSessionCount
      });
    }
  }
  onReset() {
    clearInterval(this.myInterval)
    this.setState({
      breakCount: 5,
      sessionCount: 25,
      counting: false,
      minutes: 25,
      seconds: 0
    });
  }

  count(){
    // here is the attempt 
    if (this.state.counting && this.state.seconds === 0 && this.state.minutes > 0) {
      clearInterval();
      this.setState({
        seconds: 59,
        minutes: this.state.minutes - 1
      });
    } else if (this.state.counting && this.state.seconds === 0 & this.state.minutes === 0) {
      // clearInterval();
      if (this.state.label === "Session"){
        // go ding
        this.setState({
          label: "Break",
          minutes: this.state.breakCount - 1,
          seconds: 59
        });
      } else {
        // go ding
        this.setState({
          label: "Session"
        });
      }
    }

    // here is the end of the attempt
    else if (this.state.counting && this.state.seconds > 0){
      this.myInterval = setInterval(() => {
        this.setState({
            seconds: this.state.seconds - 1
        })
    }, 1000)
    } else if (!this.state.counting){
      clearInterval(this.myInterval)
    }
  }
  onStartStop() {
    this.setState({
      counting: !this.state.counting
    });
    if(this.state.counting){
      this.count();
    }else {
      clearInterval(this.myInterval)
    }
  }
    
 
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1>Pomodoro Timer</h1>
        </header>
        <div id="bigger-container">
          <Break
            onBreakChange={this.onBreakChange}
            breakCount={this.state.breakCount}/>
          <Session
            onSessionChange={this.onSessionChange}
            sessionCount={this.state.sessionCount}/>
          <Timer 
            breakCount={this.state.breakCount}
            sessionCount={this.state.sessionCount}
            label={this.state.label}
            minutes={this.state.minutes}
            seconds={this.state.seconds}
            onReset={this.onReset}
            onStartStop={this.onStartStop}
            counting={this.state.counting}/>
        </div>
      </div>
    );
  }
  componentDidUpdate(){
    
  }
}

export default App;
